import { createAction } from 'redux-actions';
import { Goods } from '../goods/model';
import { SelectGoods, GoodsId } from './models'; 
import * as AT from '../../constants/ActionTypes';

export const selectGoods = createAction < SelectGoods, Goods >(
  AT.SELECT_GOODS,
  (goods: Goods) => ({ goods})
);

export const resetState = createAction(
  AT.RESET_STATE
);

export const increaseGoods = createAction < GoodsId, string >(
  AT.INCREASE_GOODS,
  (id: string) => ({ id})
);

export const decreaseGoods = createAction < GoodsId, string >(
  AT.DECREASE_GOODS,
  (id: string) => ({ id})
);



