import { handleActions, Action } from 'redux-actions';
import { ReceiptAmountState, ReceiptStrings, AmountConstants, AmountType, CoinCounter } from './models';
import * as AT from '../../constants/ActionTypes';

const initialState: ReceiptAmountState = <ReceiptAmountState> {
    AmountList: [],
    ReceiptAmount: 0,
    CoinCounters: [],
    openRecivedMoneyDialog: false
};

export default handleActions<ReceiptAmountState, ReceiptStrings>(
    {
        [AT.RECIVED_MONEY]: (state: ReceiptAmountState, action: Action<ReceiptStrings>): ReceiptAmountState => {
            if (!action.payload) {
                return state;
            }
            const strList  = action.payload.strList;
            if (strList.length === 0) {
                return state;
            }

            const constants = strList.map((str) => {
                const num = Number(str.replace('_', ''));
                const constant = AmountConstants.filter((ac) => {
                    return ac.value === num;
                });
                return constant[0]; 
            }).filter((c) => (c !== undefined));

            if (constants.length === 0) {
                return state;
            }

            const newState = Object.assign({}, state);
            newState.AmountList = constants;
            newState.ReceiptAmount = constants.reduce((p, c) => 
                                ({displayName: 'none', value: p.value + c.value, type: AmountType.Yen1})).value;
            const counterList: CoinCounter[] = [];
            for (let am of constants) {
                let counter = counterList.filter((cl) => (cl.type === am.type))[0];
                if (counter) {
                    counter.count++;
                } else {
                    counterList.push({
                        type: am.type,
                        count: 1
                    });
                }
            }
            newState.CoinCounters = counterList;
            newState.openRecivedMoneyDialog = true;
            return newState;
        },
        [AT.RESET_STATE]: (state: ReceiptAmountState): ReceiptAmountState => {
            const newState = Object.assign({}, state);
            newState.AmountList = [];
            newState.CoinCounters = [];
            newState.ReceiptAmount = 0;
            newState.openRecivedMoneyDialog = false;
            return newState;
        },
        [AT.CLOSE_RECEIVED_MONEY_DIALOG]: (state: ReceiptAmountState): ReceiptAmountState => {
            const newState = Object.assign({}, state);
            newState.openRecivedMoneyDialog = false;
            return newState;
        }
    }, 
    initialState
);


