import * as React from 'react';
import { StyleRules, withStyles, WithStyles } from 'material-ui/styles';
import { CoinCounter } from './models';
import ListItemText from 'material-ui/List/ListItemText';
import Typography from 'material-ui/Typography/Typography';
import Dialog from 'material-ui/Dialog';
import DialogActions from 'material-ui/Dialog/DialogActions';
import Button from 'material-ui/Button';
import DialogContent from 'material-ui/Dialog/DialogContent';
import DialogTitle from 'material-ui/Dialog/DialogTitle';
import Divider from 'material-ui/Divider';
import ListItem from 'material-ui/List/ListItem';

const styles: StyleRules<'root'|'listItem'> = {
    root: {
        width: '100%',
    },
    listItem: {
        display: 'flex',
        width: '50%',
    },
};

type ClassNames = keyof typeof styles;

interface ReceviedMoneyProps {
    coinCounterList: CoinCounter[];
    open: boolean;
    handelClose: () => void;
    enough: boolean;
}

const ReceviedMoneyDialog = withStyles(styles)<ReceviedMoneyProps>(
    (props: ReceviedMoneyProps & WithStyles<ClassNames>) => {
        const {open, classes, coinCounterList, handelClose, enough} = props;

        let text = 'おかね が たりません (´∩｀。)';
        if (enough) {
            text = '(≧∇≦)b OK!';
        }
        return (
            <Dialog
                open={open}
                onClose={handelClose}
                fullWidth={true}
            >
                <DialogTitle>
                    {text}
                </DialogTitle>
                <DialogContent>
                {
                    coinCounterList.map(ct =>  (
                        <div key={ct.type}>
                        <ListItem key={ct.type} className={classes.listItem} >
                        <ListItemText 
                            primary={
                            <Typography 
                                type="body2" 
                            >
                                {ct.type}えん
                            </Typography>}
                        />
                        <Typography
                            type="body2" 
                        >
                            {ct.count}まい
                        </Typography>
                        </ListItem>
                        <Divider />
                        </div>
                    ))
                }
                </DialogContent>
                <DialogActions>
                    <Button  color="primary" onClick={handelClose}>
                    〇
                    </Button>
                </DialogActions>
            </Dialog>
            );
    }
);

export default ReceviedMoneyDialog;


