import { handleActions, Action } from 'redux-actions';
import { BasketState, SelectGoods, GoodsCount, GoodsId,  } from './models';
import * as AT from '../../constants/ActionTypes';
import { Goods } from '../goods/model';
import { gdslist } from '../goods/constants';

const initialState: BasketState = <BasketState> {
    counterList: [],
    TotalPayment: 0
};

const decrementGoods = (goodsCountList: GoodsCount[], goods: Goods): GoodsCount[] => {
    const counter = goodsCountList.filter((r) => (r.id === goods.id))[0];
    if (counter) {
        counter.count--;
        if (counter.count <= 0) {
            goodsCountList = goodsCountList.filter((g) => (g.id !== counter.id));
        }
    }
    return goodsCountList;
};

const incrementGoods = (goodsCountList: GoodsCount[], goods: Goods): GoodsCount[] => {
        const counter = goodsCountList.filter((r) => (r.id === goods.id))[0];
        if (counter) {
            counter.count++;
        } else {
            goodsCountList.push({
                count: 1,
                img: goods.img,
                displayName: goods.displayName,
                id: goods.id,
                price: goods.price
            });
        }
        return goodsCountList;
 };

 const calcSum = (counterList: GoodsCount[]): number => {
    let sum = 0;
    for (let counter of counterList) {
        sum += (counter.price * counter.count);
    }
    return sum;
 };

export default handleActions<BasketState, SelectGoods & GoodsId>(
    {
        [AT.SELECT_GOODS]: (state: BasketState, action: Action<SelectGoods>): BasketState => {
            if (!action.payload) {
                return state;
            }
            const newState = Object.assign({}, state);
            const newConterList = Array.apply(null, newState.counterList);
            newState.counterList = incrementGoods(newConterList, action.payload.goods);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        },
        [AT.RESET_STATE]: (state: BasketState): BasketState => {
            const newState = Object.assign({}, state);
            newState.counterList = [];
            newState.TotalPayment = 0;
            return newState;
        },
        [AT.INCREASE_GOODS]: (state: BasketState, action: Action<GoodsId>): BasketState => {
            if (!action.payload) {
                return state;
            }
            const goodsId = action.payload.id;
            const newState = Object.assign({}, state);
            const newCounterList = Array.apply(null, newState.counterList);
            const goods = gdslist.filter((g) => {return g.id === goodsId; })[0];
            newState.counterList = incrementGoods(newCounterList, goods);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        },
        [AT.DECREASE_GOODS]: (state: BasketState, action: Action<GoodsId>): BasketState => {
            if (!action.payload) {
                return state;
            }
            const goodsId = action.payload.id;
            const newState = Object.assign({}, state);
            const newCounterList = Array.apply(null, newState.counterList);
            const goods = gdslist.filter((g) => {return g.id === goodsId; })[0];
            newState.counterList = decrementGoods(newCounterList, goods);
            newState.TotalPayment = calcSum(newState.counterList);
            return newState;
        }
    }, 
    initialState
);


